nventory({ characterName: this.character.name, inventory: e.target.value })\n    },\n  },\n}\n</script>\n\n<style>\n.inventory {\n  position: relative;\n}\n.inventory > textarea {\n  z-index: 1000;\n  position: absolute;\n  top: 1rem;\n  left: 1rem;\n  width: 20rem;\n  height: 30rem;\n}\n.inventory-button {\n  width: 1.5rem;\n  padding: 0 0.2rem 0 0.2rem;\n}\n</style>\n"],"sourceRoot":""}]);
