___CSS_LOADER_EXPORT___.push([module.id, "\n.combat-turn-order[data-v-3e5d73bd] {\n  display: flex;\n  flex-flow: row nowrap;\n  justify-content: space-between;\n  font-family: 'Merriweather', sans-serif;\n}\n.combat-turn-unit-container[data-v-3e5d73bd] {\n  position: relative;\n  margin-right: 0.5rem;\n}\n.combat-turn-order > span[data-v-3e5d73bd] {\n  margin-right: 0.5rem;\n}\n.combat-turn-indicator[data-v-3e5d73bd] {\n  position: absolute;\n  top: -0.8rem;\n  left: calc(50% - 5px);\n  border: solid black;\n  border-width: 0 3px 3px 0;\n  display: inline-block;\n  padding: 3px;\n  transform: rotate(45deg);\n  -webkit-transform: rotate(45deg);\n  background-color: black;\n}\n.combat-turn-indicator.on[data-v-3e5d73bd] {\n  opacity: 1;\n}\n.combat-turn-indicator.off[data-v-3e5d73bd] {\n  cursor: pointer;\n  opacity: 0.25;\n}\n.combat-turn-indicator.off[data-v-3e5d73bd]:hover {\n  opacity: 0.5;\n}\n", "",{"version":3,"sources":["webpack://./src/components/CombatTurnOrder.vue"],"names":[],"mappings":";AAqGA;EACA,aAAA;EACA,qBAAA;EACA,8BAAA;EACA,uCAAA;AACA;AACA;EACA,kBAAA;EACA,oBAAA;AACA;AACA;EACA,oBAAA;AACA;AACA;EACA,kBAAA;EACA,YAAA;EACA,qBAAA;EACA,mBAAA;EACA,yBAAA;EACA,qBAAA;EACA,YAAA;EACA,wBAAA;EACA,gCAAA;EACA,uBAAA;AACA;AACA;EACA,UAAA;AACA;AACA;EACA,eAAA;EACA,aAAA;AACA;AACA;EACA,YAAA;AACA","sourcesContent":["<template>\n  <div class=\"combat-turn-order\">\n    <span class=\"combat-turn-order-first\">First</span>\n    <div v-for=\"unit in orderedUnits\" :key=\"unit.id\" class=\"combat-turn-unit-container\">\n      <span v-if=\"unit.id === unitIdInTurn\" class=\"combat-turn-indicator on\" />\n      <span\n        v-if=\"unit.id !== unitIdInTurn\"\n        class=\"combat-turn-indicator off\"\n        @click=\"setUnitIdInTurn(unit.id)\"\n      />\n      <combat-character\n        v-if=\"unit.unitType === 'character'\"\n        :key=\"unit.id\"\n        class=\"combat-unit\"\n        :character=\"unit\"\n        draggable=\"true\"\n        @dragstart=\"onUnitDragStart($event, unit)\"\n        @drop=\"onDropOnUnit($event, unit)\"\n        @dragover.prevent\n        @dragenter.prevent\n      />\n      <combat-ally\n        v-if=\"unit.unitType === 'ally'\"\n        :key=\"unit.id\"\n        class=\"combat-unit\"\n        :monster=\"unit\"\n        draggable=\"true\"\n        @dragstart=\"onUnitDragStart($event, unit)\"\n        @drop=\"onDropOnUnit($event, unit)\"\n        @dragover.prevent\n        @dragenter.prevent\n      />\n      <combat-enemy\n        v-if=\"unit.unitType === 'enemy'\"\n        :key=\"unit.id\"\n        class=\"combat-unit\"\n        :monster=\"unit\"\n        draggable=\"true\"\n        @dragstart=\"onUnitDragStart($event, unit)\"\n        @drop=\"onDropOnUnit($event, unit)\"\n        @dragover.prevent\n        @dragenter.prevent\n      />\n    </div>\n    <span>Last</span>\n  </div>\n</template>\n\n<script>\nimport CombatEnemy from './CombatEnemy.vue'\nimport CombatAlly from './CombatAlly.vue'\nimport CombatCharacter from './CombatCharacter.vue'\nimport { mapState, mapActions } from 'vuex'\n\nexport default {\n  name: 'CombatTurnOrder',\n  components: {\n    CombatEnemy,\n    CombatAlly,\n    CombatCharacter,\n  },\n  computed: {\n    ...mapState({\n      turnOrder: state => state.combat.turnOrder,\n      characters: state => state.combat.characters,\n      enemies: state => state.combat.enemies,\n      allies: state => state.combat.allies,\n      unitIdInTurn: state => state.combat.unitIdInTurn,\n    }),\n    orderedUnits() {\n      return this.turnOrder\n        .map(id => {\n          const unit = [...this.characters, ...this.enemies, ...this.allies].find(c => c.id === id)\n          if (!unit) {\n            throw new Error(`Could not find unit with id ${id}`)\n          }\n          return unit\n        })\n        .filter(unit => unit.hitPoints > 0)\n    },\n  },\n  methods: {\n    ...mapActions('combat', ['setUnitIdInTurn', 'setTurnOrder']),\n    onUnitDragStart(event, movedUnit) {\n      event.dataTransfer.dropEffect = 'move'\n      event.dataTransfer.effectAllowed = 'move'\n      event.dataTransfer.setData('dragUnit', JSON.stringify({ movedUnit }))\n    },\n    onDropOnUnit(event, targetUnit) {\n      const { movedUnit } = JSON.parse(event.dataTransfer.getData('dragUnit'))\n      let newOrderedUnits = [...this.orderedUnits]\n      const targetUnitIndex = newOrderedUnits.findIndex(unit => unit.id === targetUnit.id)\n      newOrderedUnits = newOrderedUnits.filter(unit => unit.id !== movedUnit.id)\n      newOrderedUnits.splice(targetUnitIndex, 0, movedUnit)\n      this.setTurnOrder(newOrderedUnits.map(unit => unit.id))\n    },\n  },\n}\n</script>\n\n<style scoped>\n.combat-turn-order {\n  display: flex;\n  flex-flow: row nowrap;\n  justify-content: space-between;\n  font-family: 'Merriweather', sans-serif;\n}\n.combat-turn-unit-container {\n  position: relative;\n  margin-right: 0.5rem;\n}\n.combat-turn-order > span {\n  margin-right: 0.5rem;\n}\n.combat-turn-indicator {\n  position: absolute;\n  top: -0.8rem;\n  left: calc(50% - 5px);\n  border: solid black;\n  border-width: 0 3px 3px 0;\n  display: inline-block;\n  padding: 3px;\n  transform: rotate(45deg);\n  -webkit-transform: rotate(45deg);\n  background-color: black;\n}\n.combat-turn-indicator.on {\n  opacity: 1;\n}\n.combat-turn-indicator.off {\n  cursor: pointer;\n  opacity: 0.25;\n}\n.combat-turn-indicator.off:hover {\n  opacity: 0.5;\n}\n</style>\n"],"sourceRoot":""}]);
