achStr}${reachStr && rangeStr ? ' or ' : ''}${rangeStr}` : ''\n      const hitStr = ` Hit: ${this.damage.toString()} ${this.damageType} damage`\n      return `${attackTypeStr}${toHitStr}${reachRangeStr}${hitStr}. ${this.extra ?? ''}`\n    },\n  },\n}\n</script>\n\n<style scoped>\n.monster-action {\n  text-indent: 0;\n  display: flex;\n  flex-flow: row nowrap;\n}\n.monster-action-name {\n  font-weight: bold;\n  font-style: italic;\n  white-space: nowrap;\n}\n</style>\n"],"sourceRoot":""}]);
