itionMenu',\n  props: {\n    creature: {\n      type: Object,\n      required: true,\n    },\n  },\n  data() {\n    return {\n      isConditionMenuOpen: false,\n    }\n  },\n  computed: {\n    ...mapState({\n      conditions: state => state.campaign.conditions,\n    }),\n    availableConditions() {\n      return this.conditions.filter(\n        condition => !this.creature.conditions.some(c => c.name === condition.name),\n        'name'\n      )\n    },\n  },\n  methods: {\n    ...mapActions('party', ['addCondition', 'removeCondition']),\n    addConditionToCreature(condition) {\n      this.addCondition({ characterName: this.creature.name, conditionName: condition.name })\n    },\n    sortByKey,\n  },\n}\n</script>\n\n<style>\n.condition {\n  width: 2rem;\n  padding: 0 0.2rem;\n}\n.available-conditions {\n  display: flex;\n}\n.available-conditions {\n  display: flex;\n  flex-flow: row wrap;\n}\n.available-condition {\n  color: #58180d;\n}\n.active-condition {\n  color: green;\n}\n.condition-menu {\n  display: flex;\n  flex-flow: row wrap;\n}\n</style>\n"],"sourceRoot":""}]);
