___CSS_LOADER_EXPORT___.push([module.id, "\n.combat-overlay[data-v-fe45ae88] {\n  position: fixed;\n  right: 0;\n  top: 0;\n  width: 100%;\n  height: 100%;\n}\n.combat-overlay-mask[data-v-fe45ae88] {\n  background: rgba(0, 0, 0, 0.5);\n  width: 100%;\n  height: 100%;\n  z-index: 1;\n}\n.combat-overlay-content[data-v-fe45ae88] {\n  display: flex;\n  flex-flow: column nowrap;\n  position: fixed;\n  right: 5rem;\n  top: 1rem;\n  width: calc(100% - 10rem);\n  height: calc(100% - 2rem);\n  z-index: 1;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n.combat-turn-order[data-v-fe45ae88] {\n  margin-bottom: 1rem;\n}\n.board-container[data-v-fe45ae88] {\n  display: flex;\n  flex-flow: row nowrap;\n}\n.open-combat-overlay-button[data-v-fe45ae88] {\n  position: fixed;\n  bottom: 0.5rem;\n  right: 0.5rem;\n}\n.combat-cell.many[data-v-fe45ae88] {\n  display: grid;\n  grid-template-columns: 50% 50%;\n  grid-template-rows: 50% 50%;\n}\n.combat-cell.many > .combat-unit[data-v-fe45ae88] {\n  width: 0.75rem;\n  height: 0.75rem;\n  font-size: 0.5rem;\n}\n.combat-empty-cell[data-v-fe45ae88] {\n  width: 100%;\n  height: 100%;\n}\ntable[data-v-fe45ae88] {\n  border-collapse: collapse;\n  border-spacing: 0;\n  padding: 0;\n  margin: 0;\n}\ntr[data-v-fe45ae88],\nth[data-v-fe45ae88],\ntd[data-v-fe45ae88] {\n  border: 1px solid gray;\n  padding: 0;\n  margin: 0;\n}\ntd[data-v-fe45ae88] {\n  height: 1.5rem;\n  width: 1.5rem;\n}\n", "",{"version":3,"sources":["webpack://./src/components/CombatOverlay.vue"],"names":[],"mappings":";AAkHA;EACA,eAAA;EACA,QAAA;EACA,MAAA;EACA,WAAA;EACA,YAAA;AACA;AACA;EACA,8BAAA;EACA,WAAA;EACA,YAAA;EACA,UAAA;AACA;AACA;EACA,aAAA;EACA,wBAAA;EACA,eAAA;EACA,WAAA;EACA,SAAA;EACA,yBAAA;EACA,yBAAA;EACA,UAAA;EACA,aAAA;EACA,uBAAA;EACA,mBAAA;AACA;AACA;EACA,mBAAA;AACA;AACA;EACA,aAAA;EACA,qBAAA;AACA;AACA;EACA,eAAA;EACA,cAAA;EACA,aAAA;AACA;AACA;EACA,aAAA;EACA,8BAAA;EACA,2BAAA;AACA;AACA;EACA,cAAA;EACA,eAAA;EACA,iBAAA;AACA;AACA;EACA,WAAA;EACA,YAAA;AACA;AACA;EACA,yBAAA;EACA,iBAAA;EACA,UAAA;EACA,SAAA;AACA;AACA;;;EAGA,sBAAA;EACA,UAAA;EACA,SAAA;AACA;AACA;EACA,cAAA;EACA,aAAA;AACA","sourcesContent":["<template>\n  <div>\n    <div v-if=\"isCombatOverlayOpen\" class=\"combat-overlay\">\n      <div class=\"combat-overlay-mask\" @click=\"setIsCombatOverlayOpen(false)\" />\n      <div\n        class=\"combat-overlay-content\"\n        :style=\"{\n          background: `url(${backgroundImage})`,\n        }\"\n      >\n        <combat-turn-order />\n        <div class=\"board-container\">\n          <combat-unit-details-list />\n          <table>\n            <tr v-for=\"(row, y) in grid\" :key=\"y\">\n              <td\n                v-for=\"(cell, x) in row\"\n                :key=\"x\"\n                :class=\"{ 'combat-cell': true, many: cell.units.length > 1 }\"\n                @drop=\"onDropOnCell($event, { x, y })\"\n                @dragover.prevent\n                @dragenter.prevent\n              >\n                <combat-enemy\n                  v-for=\"unit in cell.units.filter(u => u.unitType === 'enemy')\"\n                  :key=\"unit.id\"\n                  draggable=\"true\"\n                  :monster=\"unit\"\n                  @dragstart=\"onUnitDragStart($event, unit, { x, y })\"\n                />\n                <combat-character\n                  v-for=\"unit in cell.units.filter(u => u.unitType === 'character')\"\n                  :key=\"unit.id\"\n                  draggable=\"true\"\n                  :character=\"unit\"\n                  @dragstart=\"onUnitDragStart($event, unit, { x, y })\"\n                />\n                <combat-ally\n                  v-for=\"unit in cell.units.filter(u => u.unitType === 'ally')\"\n                  :key=\"unit.id\"\n                  draggable=\"true\"\n                  :monster=\"unit\"\n                  @dragstart=\"onUnitDragStart($event, unit, { x, y })\"\n                />\n                <div v-if=\"!cell.units.length\" class=\"combat-empty-cell\" />\n              </td>\n            </tr>\n          </table>\n        </div>\n      </div>\n    </div>\n    <button\n      v-if=\"!isCombatOverlayOpen && isInCombat\"\n      class=\"open-combat-overlay-button\"\n      @click=\"setIsCombatOverlayOpen(true)\"\n    >\n      Open Combat Overlay\n    </button>\n  </div>\n</template>\n\n<script>\nimport { mapState, mapActions } from 'vuex'\nimport CombatAlly from './CombatAlly.vue'\nimport CombatEnemy from './CombatEnemy.vue'\nimport CombatCharacter from './CombatCharacter.vue'\nimport CombatTurnOrder from './CombatTurnOrder.vue'\nimport CombatUnitDetailsList from './CombatUnitDetailsList.vue'\nexport default {\n  name: 'CombatOverlay',\n  components: {\n    CombatAlly,\n    CombatEnemy,\n    CombatCharacter,\n    CombatTurnOrder,\n    CombatUnitDetailsList,\n  },\n  data() {\n    return {\n      backgroundImage: require('../img/paper.jpg'),\n    }\n  },\n  computed: {\n    ...mapState({\n      isCombatOverlayOpen: state => state.ui.isCombatOverlayOpen,\n      characters: state => state.party.characters,\n      isInCombat: state => state.combat.isInCombat,\n      enemies: state => state.combat.enemies,\n      allies: state => state.combat.allies,\n      grid: state => state.combat.grid,\n      units: state => state.combat.units,\n    }),\n  },\n  methods: {\n    ...mapActions('ui', ['setIsCombatOverlayOpen']),\n    ...mapActions('combat', ['moveUnit']),\n    onUnitDragStart(event, unit, oldPosition) {\n      event.dataTransfer.dropEffect = 'move'\n      event.dataTransfer.effectAllowed = 'move'\n      event.dataTransfer.setData('dragUnit', JSON.stringify({ unit, oldPosition }))\n    },\n    onDropOnCell(event, newPosition) {\n      const { unit, oldPosition } = JSON.parse(event.dataTransfer.getData('dragUnit'))\n      const unitRef = this.units.find(u => unit.id === u.id)\n      if (!unitRef) {\n        throw new Error(`Could not find unit with id ${unit.id}`)\n      }\n      this.moveUnit({ unit: unitRef, oldPosition, newPosition })\n    },\n  },\n}\n</script>\n\n<style scoped>\n.combat-overlay {\n  position: fixed;\n  right: 0;\n  top: 0;\n  width: 100%;\n  height: 100%;\n}\n.combat-overlay-mask {\n  background: rgba(0, 0, 0, 0.5);\n  width: 100%;\n  height: 100%;\n  z-index: 1;\n}\n.combat-overlay-content {\n  display: flex;\n  flex-flow: column nowrap;\n  position: fixed;\n  right: 5rem;\n  top: 1rem;\n  width: calc(100% - 10rem);\n  height: calc(100% - 2rem);\n  z-index: 1;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n.combat-turn-order {\n  margin-bottom: 1rem;\n}\n.board-container {\n  display: flex;\n  flex-flow: row nowrap;\n}\n.open-combat-overlay-button {\n  position: fixed;\n  bottom: 0.5rem;\n  right: 0.5rem;\n}\n.combat-cell.many {\n  display: grid;\n  grid-template-columns: 50% 50%;\n  grid-template-rows: 50% 50%;\n}\n.combat-cell.many > .combat-unit {\n  width: 0.75rem;\n  height: 0.75rem;\n  font-size: 0.5rem;\n}\n.combat-empty-cell {\n  width: 100%;\n  height: 100%;\n}\ntable {\n  border-collapse: collapse;\n  border-spacing: 0;\n  padding: 0;\n  margin: 0;\n}\ntr,\nth,\ntd {\n  border: 1px solid gray;\n  padding: 0;\n  margin: 0;\n}\ntd {\n  height: 1.5rem;\n  width: 1.5rem;\n}\n</style>\n"],"sourceRoot":""}]);
